<template>
  <view class="make-maint-record padding-bottom-normal">
    <u-navbar
        isFixed
        :borderBottom="false"
        backIconName="arrow-leftward"
        titleSize="34"
        :customBack="back"
        :background="background"
        title="保养记录表"></u-navbar>
    <view>

      <nnkjc-detail-group>
        <nnkjc-detail-item
            detailTitle="巡查地点"
            :align="'left'"
            :gap="false"
        >
          <view slot="content">
            <text>配电机房</text>
          </view>
        </nnkjc-detail-item>
      </nnkjc-detail-group>

      <view>
        <nnkjc-detail-group>

          <!--          只有填写过才展示-->
          <view v-if="keepuped">
            <nnkjc-detail-item
                detailTitle="保养内容"
                :align="'left'"
                :label-width="70"
            >
              <view slot="content" style="line-height: 1.5;">
                <view v-for="(item, index) in detailContent" :key="index">
                  {{ item }}
                </view>
              </view>
            </nnkjc-detail-item>

            <nnkjc-detail-item
                detailTitle="保养人员"
                :align="'left'"
                :label-width="70"
            >
              <view slot="content">
                {{ form.maintStaffName }}
              </view>
            </nnkjc-detail-item>
          </view>


          <nnkjc-detail-item
              :align="'left'"
              :gap="false"
          >
            <view slot="title">
              试运情况
            </view>
            <view slot="content">
              <view>
                <text>{{ normalOrNotMap[form.statusFlag] }}</text>
              </view>
            </view>
          </nnkjc-detail-item>
        </nnkjc-detail-group>
      </view>

      <view v-if="keepuped">
        <nnkjc-detail-group>
          <nnkjc-detail-item
              detailTitle="备注"
              :align="'left'"
              :gap="false"
          >
            <view slot="content">
              <text>{{ form.remarks }}</text>
            </view>
          </nnkjc-detail-item>

          <view style="padding-bottom: 10px;">
            <u-row gutter="12">
              <u-col span="2" v-for="(image, index) in keepupRecord" :key="image">
                <u-image
                    @click="doPreviewImage(index)"
                    width="50px"
                    height="50px"
                    border-radius="6"
                    :src="image"></u-image>
              </u-col>
            </u-row>
          </view>
        </nnkjc-detail-group>
      </view>


    </view>

    <u-toast ref="uToast"/>
  </view>
</template>

<script>
  import CONFIG from "../../config";

  export default {
    name: "make-patrol-record",
    data() {
      return {
        taskId: "",
        keepupRecord: [],
        keepuped: false,  //  是否已经维修过
        loading: false,
        imageUrl: [],
        action: CONFIG.baseUrl + "/maint/uploadFile",
        header: {
          token: uni.getStorageSync("token"),
        },
        formData: {
          fileKey: "keepup-record",
        },
        fileList: [],
        valueResult: "",
        resultList: [
          {
            title: "未完成",
            name: "0"
          },
          {
            title: "维修完成",
            name: "1"
          }
        ],
        resultMap: {
          "0": "未完成",
          "1": "维修完成"
        },
        form: {
          taskId: "",
          sendId: "",
          statusFlag: "",
          remarks: "",
          maintPic: "",
          content: "",
        },
        normalOrNotList: [
          {
            title: "正常",
            name: "0"
          },
          {
            title: "异常",
            name: "1"
          }
        ],
        normalOrNotMap: {
          '0': '正常',
          '1': '异常'
        },
        background: {
          'background': '#f2f2f8'
        }
      }
    },
    computed: {
      detailContent() {
        if(this.form.content) {
          return this.form.content.split('\n')
        } else {
          return []
        }
      }
    },
    watch: {},
    onLoad(options) {
      this.taskId = options.taskId;
    },
    onShow() {
      this.getMaintList();//  获取养护记录
    },
    onReady() {
    },
    methods: {
      resultChange() {
      },
      back() {
        uni.navigateBack({
          delta: 1
        })
      },
      //  获取养护的表单
      getMaintList() {
        this.$u
            .post('/maint/getWeihuList', {
              taskId: this.taskId
            }).then(res => {
          if (res.code === 200 && res.data) {
            this.form = res.data;
            this.keepuped = true;

            if (this.form.maintPic) {
              this.keepupRecord =
                  this.form.maintPic.split(",").filter(pic => {
                    return !!(pic)
                  }).map(item => {
                    return CONFIG.baseUrl + '/' + item;
                  })
            }

          } else {
            this.keepuped = false;
          }
        })
      },
      //  图片预览
      doPreviewImage(index) {
        uni.previewImage({
          urls: this.keepupRecord,
          current: index,
          success: () => {
          },
          fail: () => {
            uni.showToast({
              title: '预览图片失败',
              icon: 'none'
            });
          }
        });
      },
    }
  }
</script>

